
import { FocusMonitor } from "@angular/cdk/a11y";
import { coerceBooleanProperty } from "@angular/cdk/coercion";
import { Attribute, ChangeDetectorRef, Component, ElementRef, EventEmitter, forwardRef, Input, NgZone, OnDestroy, OnInit, Output, ViewChild, ViewEncapsulation } from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";
import { CanDisableCtor, HasTabIndexCtor, mixinDisabled, mixinTabIndex } from "tds-ui/shared/common";
import { typeSwitch } from "./type";

let nextUniqueId = 0;

export const TDS_SWITCH_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => TDSSwitch),
    multi: true
};

export class TdsSwitchChange {
    constructor(
        public source: TDSSwitch,
        public checked: boolean) { }
}

export class TdsSwitchBase {
    constructor(public _elementRef: ElementRef) { }
}
export const _TdsSwitchMixinBase: HasTabIndexCtor & CanDisableCtor & typeof TdsSwitchBase = mixinTabIndex(mixinDisabled(TdsSwitchBase))


@Component({

    moduleId: 'module.id',
    selector: 'tds-switch',
    exportAs: 'tdsSwitch',    
    templateUrl: 'switch.html',
    providers: [TDS_SWITCH_VALUE_ACCESSOR],
    encapsulation: ViewEncapsulation.None,
    host: {
        '[id]': 'id',
        class:"inline-block"
    },
})
export class TDSSwitch extends _TdsSwitchMixinBase implements OnInit, ControlValueAccessor {
    private _size: typeSwitch = 'md'
    private _disabled: boolean = false;

//     @Output() checkedA = new EventEmitter<any>();
//    inputCheck = true;


    @Input()
    get size(): typeSwitch { return this._size }
    set size(value: typeSwitch) {
        if (value !== this._size) {
            this._size = value;
            this._changeDetectorRef.markForCheck();

        }
    }
    active = false;
    private onChange = (_: any) => { };
    private onTouched = () => { };
    private _checked: boolean = false;
    @Input()
    get checked(): boolean { return this._checked; }
    set checked(value:boolean) {
        if (value!=this.checked) {
            this._checked=value;
            this._changeDetectorRef.markForCheck()
            
        }
    }
    @Input()
    get disabled() { return this._disabled }
    set disabled(value: any) {
        const newValue = coerceBooleanProperty(value);
        if (newValue !== this.disabled) {
            this._disabled = newValue;
            this._changeDetectorRef.markForCheck();

        }
    }
    @Output() readonly change: EventEmitter<TdsSwitchChange> =
        new EventEmitter<TdsSwitchChange>();
    @ViewChild('input') _inputElement!: ElementRef;

    constructor(elementRef: ElementRef,
        private _focusMonitor: FocusMonitor,
        private _changeDetectorRef: ChangeDetectorRef,
        @Attribute('tabindex') tabIndex: string,
    ) {
        super(elementRef);
        this.tabIndex = parseInt(tabIndex) || 0;
    }
    ngOnInit(): void {
        // this.inputCheck; debugger
    }

    _onTouched: () => any = () => { };

    _onChangeEvent(event: any) {
        event.stopPropagation();
        this.checked = this._inputElement.nativeElement.checked;
        this._emitChangeEvent();
    }

    _onInputClick(event: Event) {
        event.stopPropagation();
    }

    writeValue(value: any): void {
        this.checked = !!value;
    }

    registerOnChange(fn: any): void {
        this.onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }

    setDisabledState(isDisabled: boolean): void {
        this.disabled = isDisabled;
        this._changeDetectorRef.markForCheck();
    }

    focus(): void {
        this._focusMonitor.focusVia(this._inputElement.nativeElement, 'keyboard');
    }

    toggle(): void {
        if (!this.disabled) {
          this.checked = !this.checked;
        //   this.checkedA.emit(this.checked);
        } 
    }

    private _emitChangeEvent() {
        this.onChange(this.checked);
        this.change.emit(new TdsSwitchChange(this, this.checked));
    }


}

